.repo {
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    border-radius: 0.5rem;
    border: 3px solid rgba(30, 50, 75, 0.9);
    color: black;
}

.repo-top {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    column-gap: 1rem;
    font-weight: bold;
}

.repo-name {
    font-size: 2.5rem;
    font-weight: bold;
    text-decoration: underline;
}

.repo-star {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    column-gap: 0.5rem;
    font-size: 1.2rem;
}

.repo-link,
.homepage-link {
    display: flex;
    align-items: center;
    justify-content: center;
}

.repo-link a,
.homepage-link a {
    width: max-content;
    padding: 0.5rem 1rem;
    outline: none;
    border: 2px solid rgba(30, 50, 75, 0.9);
    border-radius: 5px;
    font-weight: 500;
    font-size: 1.2rem;
    text-transform: capitalize;
}

.repo-link a:hover,
.homepage-link a:hover {
    background-color: rgba(30, 50, 75, 0.9);
    cursor: pointer;
    color: white;
}

.description {
    font-size: 1.8rem;
}

.language {
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: capitalize;
}
